			padding-left:0.5rem;
			height:1.8rem;
			line-height:1.8rem;
		}
		.selectBox{
			background:#fff;
			position:relative;
			&::before,&::after{
				content: "";
				display:table;
			}
			.chooseParent,.awardParent{
				width:9rem;
				overflow: hidden;
				position:relative;
				border:1px solid #CACACA;
				margin-left:1rem;
				span{
					height:1.6rem;
					position:absolute;
					top:0;
					right:1.5rem;
					border-left:1px solid #CACACA;
				}
			}
			.chooseParent{
				margin-bottom:0.6rem;
				margin-top:1rem;
			}
			.awardParent{
				margin-bottom:1rem;
			}
			select{
				width:10rem;
				height:1.6rem;
				padding-left:0.3rem;
				@include sc(0.7rem,#4C4C4C);
				background:url('../../assets/images/arrow.png') right center no-repeat;
				background-size:1rem;
				background-position-x:7.8rem;
