					@include sc(0.7rem,#4C4C4C);
				}
			}
			.searchBtn{
				width:3.5rem;
				height:1.6rem;
				line-height: 1.6rem;
				@include sc(0.7rem,#fff);
				@include borderRadius(0.1rem);
				background:#F35353;
				text-align:center;
				position:absolute;
				bottom:1rem;
				right:0.5rem;
			}
		}
	}
	.lottery-section-bottom{
		h4{
			@include sc(0.7rem,#4C4C4C);
			padding-left:0.5rem;
			height:1.8rem;
			line-height:1.8rem;
		}
		ul{
			width:100%;
			background:#fff;
			padding:0 0.5rem;
			li{
				width:100%;
				margin:0 auto;
				p{
					width:29%;
					height:1.8rem;
					line-height: 1.8rem;
					text-align:center;
					float:left;
					@include sc(0.7rem,#929292);
				}
				p:last-child{
					width:13%;
				}
			}
			li:first-child{
				P{	
					height:1.5rem;
					line-height: 1.5rem;
					color:#4C4C4C;
				}
			}
			li:not(:first-child){
				border-top:1px solid #D9D9D9;
				p:last-child{
					color:#F46A6A;
				}
			}
		}
	}
</style>


